*,
*::before,
*::after {

  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


html {
  
  font-size: 62.5%;

  @include respond(tab-land) {
    font-size: 60%
  }

  @include respond(tab-port) {
    font-size: 55%
  }

  @include respond(phone) {
    font-size: 50%
  }
}

body {
  font-family: 'Poppins', sans-serif;
  color: $color-grey-dark-3;
  overflow: hidden;
  //transform: rotate(180deg);
}

.welcome {
  position: absolute;
  top: 1rem;
  left: 1rem;
}

canvas {
  margin: 0 auto;
  border: thin solid black;
}
